.restart {
	position: absolute;
	/* top: 1rem;
	left: 1rem; */
	left: 50%;
	transform: translateX(-50%);
	bottom: 6rem;
	font-size: 1.5rem;
	color: var(--color-border-cell);
	background-color: transparent;
	border: 0;
	cursor: pointer;
	text-transform: uppercase;
	border-bottom: 2px solid currentColor;
	padding: 0;
	transition: padding ease-out 300ms;
}

.restart--grey {
	color: var(--color-border-cell-grey);
}

.restart:hover {
	padding: 0 0 2px 4px;
}